export default {
	data() {
		return {
			art:'',
		};
	},
	onLoad(options) {
		this.get_art_detail(options.id)
	},
	methods:{
		get_art_detail(id){
			this.$api.http.get('article/get_one_article?id='+ id).then(res=>{
				this.art = res.data
			})
		}
	},
	onPullDownRefresh() {
		//this._load()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
